.form-control {
  display: block;
  width: 100%;
  padding: 13px;
  border: 2px solid $input-border;
  border-radius: $input-rounded;
	outline: none;

	&--focus{
		opacity: 0.4;
		border-color: #999;
		
		&:focus {
			outline: none;
			opacity: 1;
			border-color: $input-border;
			color: #383737;
		+ .form-control--icon{
			color: #383737;
			}
		}
	}
	&--icon{
		position: absolute;
		color:#c6ccd2;
		left:17px;
		top:16px;
		z-index:1;
	}

	&--light::placeholder{
	color: $input-color-placeholder-light;
	}
	
	&--dark{
	color: $input-color-placeholder-dark;
}
	
	&--subscribing{
		margin-bottom: 0;	
		max-width: 75%;
	}
}
.inner-addon {
  position: relative;
}
.left-addon input {
  padding-left: 45px;
}
.form-group{
	margin-bottom: 1.5rem;
}

.checkbox-wrap{
	text-align: left;
	
	.checkbox__input{
		display: none;
	}
	
	.checkbox__input + .checkbox__label:before {
    content: '';
		vertical-align: top;
		display: inline-block;
    height: 16px;
    width: 16px;
    background-color: #fff;
    border-radius: $checkbox-rounded;
    margin-right: 7px;
    border: 1px solid #b4bbc2;
	}
	.checkbox__input:checked + .checkbox__label:before {
    content: url('data:image/png;base64,');
		font-size: 12px;
		font-weight: bold;
    text-align: center;
    background-color: #24aa98;
    color: #fff;
    border: 1px solid #24aa98;
	}
}

