body {
	font-family: $ff-main;
	font-size: 1rem;
	line-height: 1.2;
}

html {
	box-sizing: border-box;
}


*, *:before, *:after {
	box-sizing: inherit;
}

ul,
ol {
	list-style-type: none;
}

.clearfix {
	
	&:after {
		content: '';
		display: table;
		clear: both;
	}
}
a{
	text-decoration: none;
	color: #b4bbc2;
}